                     <span></span>
                  </el-dropdown-item>

                  <el-dropdown-item @click.native=updateName(data)>
                     <span></span>
                  </el-dropdown-item>

                  <el-dropdown-item @click.native=setPrincipal(data)>
                     <span></span>
                  </el-dropdown-item>

